@media only screen and (max-width: 1440px) {
    body {
        .tparrows {
            &.tp-leftarrow {
                transform: none !important;
                -webkit-transform: none !important;
                -moz-transform: none !important;
                left: 0 !important;
                margin: -22px 0 0 0 !important;
            }
            &.tp-rightarrow {
                transform: none !important;
                -webkit-transform: none !important;
                -moz-transform: none !important;
                left: auto !important;
                right: 0 !important;
                margin: -22px 0 0 0 !important;
            }
        }
        &.boxed_layout {
            .tparrows {
                &.tp-leftarrow {
                    left: 18px !important;
                }
                &.tp-rightarrow {
                    right: 18px !important;
                }
            }
            .slick_prev {
                left: -40px;
            }
            .slick_next {
                right: -40px;
            }
        }
    }

    body.header_style_8 .side_nav .main_menu_nav > li > a {
        padding: 6px 0;
        &:after {
            top: 5px !important;
        }
    }
}

