                background: #eeeeee;
                padding: 4px 23px;
            }
            .header_socials a {
                margin: 5px 30px 5px 0;
                &:first-child {
                    margin: 5px 30px 5px 0;
                }
                &:last-child {
                    margin: 0;
                }
            }
            .logo_wrapper {
                position: relative;
                background: #fff;
                padding: 22px 35px 30px 41px;
