            }
            .header_info {
                background: $stm_base_color;
            }
            .icon_texts {
                padding: 40px 40px;
            }
            .header_info .icon_text {
                padding: 0;
                color: #aaaaaa;
                display: inline-block;
                vertical-align: top;
                margin-right: 70px;
                &:last-child {
                    margin: 0;
                }
                .text {
                    strong {
                        color: #fff;
                        a {
                            color: #fff;
                            font-size: 15px;
                        }
                    }
                    span {
                        font-size: 13px;
                    }
                }
            }
        }
        .icon_text .text {
            float: left;
        }
        .mobile_header .header_info .icon_text .icon {
            width: 32px;
            margin: 0;
        }
        #menu_toggle {
            position: relative;
            cursor: pointer;
            float: right;
            width: 26px;
            height: 20px;
            padding: 10px 0;
            margin: 17px 0 0;
            button {
                position: absolute;
                left: 0;
                top: 50%;
                margin: -2px 0 0;
                background: $stm_base_color;
                height: 4px;
                padding: 0;
                border: none;
                width: 100%;
                -webkit-transition: 0.3s;
                -moz-transition: 0.3s;
                -o-transition: 0.3s;
                transition: 0.3s;
                outline: none !important;
                @include border-radius(2px);
                &:before {
                    content: '';
                    position: absolute;
                    left: 0;
                    top: -8px;
                    width: 26px;
                    height: 4px;
                    background: $stm_base_color;
                    -webkit-transform-origin: 1.5px center;
                    -moz-transform-origin: 1.5px center;
                    -o-transform-origin: 1.5px center;
                    transform-origin: 1.5px center;
                    -webkit-transition: 0.3s;
                    -moz-transition: 0.3s;
                    -o-transition: 0.3s;
                    transition: 0.3s;
                    @include border-radius(2px);
                }
                &:after {
                    content: '';
                    position: absolute;
                    left: 0;
                    bottom: -8px;
                    width: 26px;
                    height: 4px;
                    background: $stm_base_color;
                    -webkit-transform-origin: 1.5px center;
                    -moz-transform-origin: 1.5px center;
                    -o-transform-origin: 1.5px center;
                    transform-origin: 1.5px center;
                    -webkit-transition: 0.3s;
                    -moz-transition: 0.3s;
                    -o-transition: 0.3s;
                    transition: 0.3s;
                    @include border-radius(2px);
                }
            }
            &.open button {
                background: transparent;
                &:before {
                    top: 0;
                    -webkit-transform: rotate3d(0, 0, 1, -45deg);
                    -moz-transform: rotate3d(0, 0, 1, -45deg);
                    -o-transform: rotate3d(0, 0, 1, -45deg);
                    transform: rotate3d(0, 0, 1, -45deg);
                    -webkit-transform-origin: 50% 50%;
                    -moz-transform-origin: 50% 50%;
                    -o-transform-origin: 50% 50%;
                    transform-origin: 50% 50%;
                }
                &:after {
                    bottom: 0;
                    transform: rotate3d(0, 0, 1, 45deg);
                    transform-origin: 50% 50%;
                }
            }
        }
        .mobile_header .top_nav_mobile {
            padding: 16px 0;
            margin: 0 0 15px;
            display: none;
            box-shadow: 0 0 5px rgba(0, 0, 0, 0.52);
            .main_menu_nav {
                padding: 0;
                margin: 0;
                li {
                    position: relative;
                    list-style: none;
                }
                > li {
                    > a {
                        padding: 11px 36px 11px 44px;
                        display: block;
                        font-size: 20px;
                        color: #ffffff;
                        text-transform: lowercase;
                        font-weight: 700;
                        letter-spacing: -0.04em;
                    }
                    &:hover {
                        & > a {
                            color: $stm_third_color;
                        }
                    }
                    &.menu-item-has-children {
                        .arrow {
                            position: absolute;
                            display: block;
                            right: 12px;
                            top: 0;
                            cursor: pointer;
                            height: 50px;
                            padding: 0 30px;
                            @include transition();
                            &.active {
                                -moz-transform: rotate(45deg);
                                -ms-transform: rotate(45deg);
                                -webkit-transform: rotate(45deg);
                                -o-transform: rotate(45deg);
                                transform: rotate(45deg);
                                i {
                                    &:before, &:after {
                                        background: $stm_third_color;
                                    }
                                }
                            }
                            i {
                                position: absolute;
                                left: 50%;
                                top: 50%;
                                transform: translate(-50%, -50%);
                                -moz-transform: translate(-50%, -50%);
                                -webkit-transform: translate(-50%, -50%);
                                width: 11px;
                                height: 11px;
                                &:before {
                                    content: '';
                                    position: absolute;
                                    left: 0;
                                    top: 4px;
                                    width: 11px;
                                    height: 3px;
                                    background: #fff;
                                    @include border-radius(1px);
                                }
                                &:after {
                                    content: '';
                                    position: absolute;
                                    left: 4px;
                                    top: 0;
                                    width: 3px;
                                    height: 11px;
                                    background: #fff;
                                    @include border-radius(1px);
                                }
                            }
                        }
                        > .sub-menu > li.menu-item-has-children .arrow {
                            padding: 10px 20px 12px;
                        }
                    }
                }
                ul {
                    position: relative;
                    margin: 0;
                    padding: 0;
                    display: none;
                    background: #f0f2f5;
                    &:before {
                        content: '';
                        position: absolute;
                        right: 31px;
                        top: 0;
                        border-left: 10px solid transparent;
                        border-right: 10px solid transparent;
                        border-top: 8px solid $stm_base_color;
                    }
                    li {
                        text-transform: capitalize;
                        .arrow {
                            display: none !important;
                        }
                        a {
                            font-size: 16px;
                            color: $stm_base_color;
                            font-weight: 600;
                            padding: 11px 36px 11px 44px;
                            display: block;
                        }
                        &:active > a {
                            color: $stm_third_color;
                        }
                        ul {
                            display: block;
                            background: #fff;
                            &:before {
                                display: none;
                            }
                            a {
                                font-size: 14px;
                                font-weight: 400;
                            }
                            li {
                                position: relative;
                                padding: 0 0 0 7px;
                                &:before {
                                    content: '';
                                    position: absolute;
                                    left: 44px;
                                    top: 50%;
                                    width: 4px;
                                    height: 1px;
                                    background: $stm_base_color;
                                }
                            }
                        }
                    }
                }
                li {
                    &.current_page_item > a, &.current-menu-parent > a, &.current-menu-ancestor > a {
                        color: $stm_third_color;
                    }
                }
                > li.current_page_item.menu-item-has-children > .arrow {
                    color: $stm_base_color;
                }
                > li {
                    &.current-menu-parent.menu-item-has-children > .arrow, &.current-menu-ancestor.menu-item-has-children > .arrow, &.current-menu-parent.menu-item-has-children > .sub-menu > li.current-menu-item.menu-item-has-children > .arrow, &.current-menu-ancestor.menu-item-has-children > .sub-menu > li.current-menu-parent.menu-item-has-children > .arrow {
                        color: $stm_base_color;
                    }
                }
            }
        }
        .mobile_menu > ul ul ul {
            display: block;
        }
    }
}

