    }
    &.style_1 {
        .works_filter {
            list-style: none;
            padding: 14px 20px;
            margin: 0 0 40px;
            background: $stm_third_color;
            @include cleaner();
            li {
                float: left;
                line-height: 1;
                font-size: 14px;
                &.active {
                    a {
                        background: $stm_base_color;
                        color: #fff;
                        @include border-radius(5px);
                    }
                }
                a {
                    color: $stm_base_color;
                    display: block;
                    padding: 9px 15px;
                    text-decoration: none !important;
                    &:focus {
                        text-decoration: none;
                    }
                }
            }
        }
    }
    &.style_2 {
        .works_filter {
            list-style: none;
            margin: 0 0 33px;
            padding-left: 0;
            @include cleaner();
            li {
                float: left;
                &:not(:first-child) {
                    margin-left: 39px;
                }
                line-height: 1;
                &.active {
                    a {
                        background: $stm_secondary_color;
                        color: $stm_base_color;
                    }
                }
                a {
                    color: $stm_base_color;
                    display: block;
                    text-transform: uppercase;
                    padding: 17px 18px 15px 18px;
                    font-size: 13px;
                    font-weight: 900;
                    letter-spacing: -0.05em;
                    @include transition();
                    &:hover,
                    &:focus {
                        text-decoration: none;
                    }

                    &:hover {
                        background: $stm_secondary_color;
                        color: $stm_base_color;
                    }
                }
            }

            &.container {
                margin-right: auto;
                margin-left: auto;
                padding-left: 15px;
                padding-right: 15px;
            }

            .works_filter_switcher {
                float: right;

                .stm_works_grid_switcher {
                    position: relative;
                    display: block;
                    float: right;
                    width: 45px;
                    height: 45px;
                    top: -2px;
                    border: 3px solid #aaa;
                    color: #aaa !important;
                    margin: 2px 0 0;
                    transition: all 0.2s ease;

                    &:hover {
                        border-color: $stm_secondary_color;
                        background: transparent;

                        .fa {
                            color: $stm_secondary_color;
                        }
                    }
                }

                .stm_works_grid_switcher .fa {
                    font-size: 13px;
                    line-height: 13px;
                    position: absolute;
                    top: 13px;
                    transition: color .2s ease, left 0.2s ease, right 0.2s ease;
                }

                .stm_works_grid_switcher .left {
                    left: 5px;
                }

                .stm_works_grid_switcher .right {
                    right: 5px;
                }

                .stm_works_grid_switcher.active .left {
                    left: 22px;
                }

                .stm_works_grid_switcher.active .right {
                    right: 22px;
                }
            }
        }
    }
}

.stm_works_wr.grid.style_2 {
    .stm_works_masonry_disabled {
        margin: 0 -15px;
        .item {
            float: left;
            position: relative;
            margin: 15px;
            a {
                position: absolute;
                top: 0;
                left: 0;
                display: flex;
                align-items: flex-end;
                text-decoration: none;
                width: 100%;
                height: 100%;
                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    opacity: 0;
                    background-image: -webkit-linear-gradient( 90deg, rgb(38,17,0) 0%, rgba(38,17,0,0) 100%);
                    @include transition();
                }
                .work-title {
                    display: block;
                    position: relative;
                    top: -20px;
                    max-width: 330px;
                    padding: 44px 30px 26px;
                    font-family: 'Mina', sans-serif;
                    font-weight: 600;
                    letter-spacing: -.2px;
                    font-size: 20px;
                    line-height: 24px;
                    color: #fff;
                    opacity: 0;
                    @include transition();
                    &:before {
                        position: absolute;
                        top: 27px;
                        left: 30px;
                        content: '';
                        display: block;
                        width: 44px;
                        height: 4px;
                        background-color: $stm_secondary_color;
                        @include transition();
                    }
                }
                .work-description {
                    display: block;
                    padding-top: 14px;
                    position: relative;
                    top: 40px;
                    letter-spacing: 0px;
                    @include transition();
                    font-family: 'Exo', sans-serif;
                    font-weight: 400;
                    font-size: 13px;
                    color: #fff;
                }
                &:hover {
                    text-decoration: none;
                    &:before {
                        opacity: 1;
                    }
                    .work-title {
                        opacity: 1;
                        top: 0;
                        &:before {
                            top: 22px;
                        }
                        .work-description {
                            top: 0;
                        }
                    }
                }
            }
            img {
                display: block;
                max-width: 100%;
                height: auto;
            }
        }
    }
}