body .vc_row.overlay.vc_parallax {
    .vc_parallax-inner {
        &:before {
            content: '';
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            background: rgba(0, 0, 0, .25);
        }
    }
}

body .vc_row.overlay {
    position: relative;
    &:before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background: rgba(0, 0, 0, .25);
        display: block;
    }
}

