.stm_events_grid {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    position: relative;
    margin: 0 -15px;
    font-size: 0;
    .item {
        position: relative;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        vertical-align: top;
        width: 33.333333%;
        padding: 0 15px;
        margin: 0 0 30px;
        font-size: 14px;
        .item_wr {
            border: 3px solid #f2f2f2;
            .item_thumbnail {
                margin: -3px -3px 0;
                img {
                    display: block;
                    max-width: 100%;
                    height: auto;
                }
                a {
                    position: relative;
                    display: block;
                    &:before {
                        content: '';
                        position: absolute;
                        left: 0;
                        top: 0;
                        right: 0;
                        bottom: 0;
                        background: rgba(#000, .6);
                        @include transition();
                        opacity: 0;
                    }
                    &:after {
                        content: "\e920";
                        @include theme_icons();
                        @include transition();
                        position: absolute;
                        left: 50%;
                        top: 50%;
                        margin: -15px 0 0 -15px;
                        color: #fff;
                        font-size: 30px;
                        opacity: 0;
                    }
                    &:hover {
                        &:before, &:after {
                            opacity: 1;
                        }
                    }
                }
            }
            .content {
                padding: 20px 27px;
                font-size: 13px;
                @include transition();
                h6 {
                    padding: 0;
                    margin: 0 0 14px;
                    line-height: 24px;
                    a {
                        text-decoration: none !important;
                        color: $stm_base_color;
                        @include transition();
                        &:hover {
                            color: $stm_secondary_color;
                        }
                    }
                    &:after {
                        display: none;
                    }
                }
                p {
                    line-height: 18px;
                }
                .stm-event__meta {
                    padding: 0;
                    margin: 0;
                    list-style: none;
                    li {
                        display: block;
                        margin: 6px 0;
                        position: relative;
                        padding-left: 24px;
                        line-height: 18px;
                        font-family: Roboto, arial, serif;
                        .fa {
                            position: absolute;
                            top: 2px;
                            left: 0;
                            width: 17px;
                            text-align: center;
                            color: $stm_third_color;
                            font-size: 15px;
                        }
                    }
                }
            }
        }
    }
    &.cols_4 {
        .item {
            width: 25%;
        }
    }
    &.cols_2 {
        .item {
            width: 50%;
        }
    }
    &.cols_1 {
        .item {
            width: 100%;
        }
    }
}

