            &:before {
                display: none;
                content: "";
                position: absolute;
                top: 0;
                left: -1px;
                width: 5px;
                height: 100%;
                background-color: $stm_third_color;
            }
            .item_wr {
                .content {
                    display: table;
                    width: 100%;
