                        display: table-cell;
                        vertical-align: middle;
                        width: 240px;
                        &:first-child {
                            width: 480px;
                        }
                        h5 {
                            padding: 0;
                            margin: 0 0 0;
                            line-height: 24px;
                            letter-spacing: -0.70px;
                            font-size: 16px;
