                                text-decoration: none !important;
                                color: $stm_base_color;
                                @include transition();
                                &:hover {
                                    color: $stm_secondary_color;
                                }
                            }
                            &:after {
                                display: none;
                            }
                        }
                        .event_speaker_row {
                            margin: 15px 0;
                        }
                        .event_speaker_thumbnail {
                            float: left;
                            display: inline;
                            width: 40px;
                            height: 40px;
                            margin-right: 15px;
                            img {
                                display: block;
                                max-width: 100%;
                                height: auto;
                                @include border-radius(50%);
                            }
                        }
                        .event_speaker_content {
                            position: relative;
                            overflow: hidden;
                            font-size: 13px;
                            .event_speaker_description {
                                font-style: italic;
                                margin-bottom: 4px;
                            }
                            .event_speaker_name {
                                letter-spacing: -0.70px;
                                font-family: Mina;
                                font-weight: 600;
                                color: #222222;
                                a {
                                    text-decoration: none !important;
                                    color: #222222;
                                    @include transition();
                                    &:hover {
                                        color: $stm_secondary_color;
                                    }
                                }
                            }
