                        .fa {
                            position: absolute;
                            top: 2px;
                            left: 0;
                            width: 20px;
                            text-align: center;
                            margin-right: 10px;
                            color: $stm_third_color;
                            font-size: 15px;
                        }
                        .event__calendar {
                            position: relative;
                            padding-left: 28px;
                            font-size: 13px;
                            .fa {
                                top: 50%;
                                margin-top: -7px;
                            }
                        }
                        .event__venue {
                            position: relative;
                            padding-left: 24px;
                            font-size: 13px;
                            .fa {
                                top: 50%;
                                margin-top: -8px;
                            }
                        }
                    }
                }
            }
            &:hover {
                -webkit-box-shadow: 0px 2px 15px 0px rgba(0, 0, 0, 0.3);
                -moz-box-shadow: 0px 2px 15px 0px rgba(0, 0, 0, 0.3);
                box-shadow: 0px 2px 15px 0px rgba(0, 0, 0, 0.3);
                &:before {
                    display: block;
                }
