                padding: 8px 0;
                font-size: 13px;
                color: #222222;
            }
            .event_lesson_speakers {
                display: flex;
                align-items: flex-start;
                flex-direction: row;
                flex-wrap: wrap;
                list-style: none;
                margin: 0 -25px;
                padding: 0;
                li {
                    display: inline-block;
                    vertical-align: top;
                    padding: 15px 25px;
                    width: 33.333333%;
                    .event_speaker_thumbnail {
                        max-width: 40px;
                        margin-right: 14px;
                        float: left;
                        img {
                            display: block;
                            max-width: 100%;
                            height: auto;
                            @include border-radius(50%);
                        }
                    }
                    .event_speaker_content {
                        display: block;
                        position: relative;
                        overflow: hidden;
                        font-size: 13px;
                        .event_speaker_name {
                            margin-bottom: 2px;
                            letter-spacing: -0.30px;
                            font-family: Mina, sans-serif;
                            font-weight: 700;
                            a {
                                color: #222222;
                                &:hover {
                                    text-decoration: none;
                                    color: $stm_secondary_color;
                                }
                            }
                        }
                        .event_speaker_description {
                            font-style: italic;
                        }
                    }
                }
            }
        }
    }
    li {
        &:last-child {
            .event_lesson_info_content_wrap {
                padding-bottom: 0;
                margin-bottom: 0;
                &:before {
                    display: none;
                }
                .event_lesson_info_content {
                    border-bottom: 0;
                }
            }
        }
    }
}

@media only screen and (max-width: 767px) {
    .events_lessons_box {
        display: block;
        margin-bottom: 25px;
    }
    .event_lesson_tabs {
        border-top: 1px solid #ffffff;
        &.active {
            border: 0;
        }
        &:first-child {
            border: 0;
        }
    }
}

@media only screen and (max-width: 680px) {
    .event_lesson_info {
        padding-left: 0;
        .event_lesson_info_content_wrap {
            .event_lesson_info_content {
                margin-left: 25px;
                .event_lesson_speakers {
                    li {
                        width: 50%;
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 414px) {
    .event_lesson_info {
        .event_lesson_info_content_wrap {
            .event_lesson_info_content {
                .event_lesson_speakers {
                    li {
                        width: 100%;
                    }
                }
            }
        }
    }
}

.stm_events_tiles {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
    .stm_events_tile {
        width: 50%;
        .inner {
            display: flex;
            flex-direction: row;
            align-items: center;
            flex-grow: 1;
            margin: 0 15px;
            background-color: #f7f7f7;
            .stm_events_tiles__image {
                min-width: 255px;
            }
            .stm_events_widgets {
                padding: 15px 30px;
                margin: 0;
                .item {
                    padding: 0;
                }
                .content {
                    h6 {
                        font-size: 22px;
                        line-height: 26px;
                        letter-spacing: -.6px;
                    }
                }
            }
        }
    }
}