.wpb_content_element {
    ul {
        margin: 0 0 20px;
        list-style: none;
        padding: 0;
        li {
            position: relative;
            padding: 0 0 0 26px;
            margin: 0 0 9px;
            line-height: 22px;
            color: #222222;
            &:before {
                content: "\f04d";
                @include fa();
                position: absolute;
                left: 0;
                top: 8px;
                display: inline-block;
                vertical-align: top;
                font-size: 5px;
                color: $stm_secondary_color;
                transform: rotate(45deg);
            }
        }
    }
    strong {
        color: #fff;
    }
}

