}

.overlapping {
    position: relative;
    z-index: 1000;
}

.stm_wave_pulse {
    width: 1px;
    height: 0;
    margin: 0 auto;
    &:after,
    &:before {
        content: '';
        display: block;
        position: absolute;
        width: 700px;
        height: 700px;
        margin: -150px 0 0 -350px;
        border-radius: 50%;
        border: 1px solid #454648;
        animation: stm_zoom_big 5.25s linear 5.25s infinite;
    }
    &:before {
        animation-delay: .75s;
    }
    &_2 {
        &:after {
            animation-delay: 1.5s;
        }
        &:before {
            animation-delay: 2.25s;
        }
    }
    &_3 {
        &:after {
            animation-delay: 3s;
        }
        &:before {
            animation-delay: 3.75s;
        }
    }
    &_4 {
        &:after {
            animation-delay: 4.5s;
        }
        &:before {
            animation-delay: 5.25s;
        }
    }
}

